/*
Name: StarWarsIntro.css
URI: polarnotion.github.io/starwarsintro
Author: Polar Notion
Author URI: http://polarnotion.com/
Description: A simple CSS library for creating a Star Wars Intro Crawl. May the Force be with you.
Version: 1.0
Thanks: Open Source coders for the starter template had to modify for my application.
*/

.star-wars-intro {
  background: url(/proj4-simple-over-complex/static/media/starsBg-wide.768eb96f.jpg) center center;
  width: 100%;
  height: 100%;
  font-family: "StarJedi", arial, verdana, sans-serif;
  font-weight: 700;
  color: #ebd71c;
  background-color: #000;
  overflow: hidden;
  position: relative;
}

.star-wars-intro p.intro-text {
  position: relative;
  max-width: 16em;
  font-size: 200%;
  font-weight: 400;
  margin: 20% auto;
  color: #4ee;
  opacity: 0;
  z-index: 1;
  text-align: center;
  -webkit-animation: intro 2s ease-out;
  animation: intro 2s ease-out;
}

.star-wars-intro .main-content {
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  z-index: 3;
  width: 98%;
  height: 50em;
  bottom: 0;
  font-size: 80px;
  font-weight: bold;
  text-align: justify;
  overflow: hidden;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  -webkit-transform: perspective(350px) rotateX(25deg);
          transform: perspective(350px) rotateX(25deg);
}

.star-wars-intro .main-content:after {
  position: absolute;
  content: " ";
  top: 0;
  bottom: 60%;
  background-image: -webkit-gradient(
    linear,
    left bottom, left top,
    from(rgba(0, 0, 0, 1)),
    to(transparent)
  );
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 1) 0%,
    transparent 100%
  );
  pointer-events: none;
}

.star-wars-intro .space-button {
  color: #ebd71c;
  border: 12px solid rgb(241, 223, 53);
  padding: 20px;
  background: transparent;
  text-decoration: none;
  margin: 0 auto;
  display: block;
  text-align: center;
}

.star-wars-intro .space-button:hover {
  background-color: #d2be03;
  border-color: #b8a40a;
  color: black;
}

.star-wars-intro .space-button:active,
.star-wars-intro .space-button:focus {
  background-color: #b8a40a;
  border-color: #d2be03;
  color: black;
}

.star-wars-intro .title-content {
  position: absolute;
  top: 100%;
  -webkit-animation: scroll 160s linear 10s forwards;
          animation: scroll 160s linear 10s forwards;
}

.star-wars-intro .title-content > .content-header {
  text-align: center;
}
.content-header {
  font-size: 200%;
}
.content-body {
  font-size: 80%;
  font-family: "StarJedi", arial, verdana, sans-serif;
}

/* Main Image Styles */

.star-wars-intro .main-logo {
  position: absolute;
  width: 2.6em;
  left: 50%;
  top: 10px;
  font-size: 10em;
  text-align: center;
  margin-left: -1.3em;
  line-height: 0.8em;
  letter-spacing: -0.05em;
  color: #000;
  text-shadow: -2px -2px 0 #ebd71c, 2px -2px 0 #ebd71c, -2px 2px 0 #ebd71c,
    2px 2px 0 #ebd71c;
  opacity: 0;
  z-index: 1;
  -webkit-animation: logo 7s ease-out 2.5s;
  animation: logo 7s ease-out 2.5s;
}

.star-wars-intro .main-logo > img {
  max-width: 100%;
}

@-webkit-keyframes intro {
  0% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes intro {
  0% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes logo {
  0% {
    -webkit-transform: scale(1);
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0.1);
    opacity: 0;
  }
}

@keyframes logo {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0;
  }
}

@-webkit-keyframes scroll {
  0% {
    top: 100%;
  }
  100% {
    top: -170%;
  }
}

@keyframes scroll {
  0% {
    top: 100%;
  }
  100% {
    top: -170%;
  }
}

@media screen and (max-width: 720px) {
  .star-wars-intro .main-content {
    font-size: 35px;
  }
  .star-wars-intro .title-content {
    position: absolute;
    top: 100%;
    -webkit-animation: scroll 100s linear 8s forwards;
            animation: scroll 100s linear 8s forwards;
  }
}

