.App {
  text-align: center;
}

.App-header {
  background-image: radial-gradient(
    rgb(129, 165, 231),
    rgb(45, 102, 50),
    rgb(40, 216, 40)
  );
  height: 140px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  color: rgb(59, 45, 45);
  -webkit-text-stroke: 3px rgb(255, 255, 255);
}
nav {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: skyblue;
}

.link {
  color: #b4cec9;
  padding: 15px 20px;
  background-color: blue;
  margin: 5px;
  text-decoration: none;
  border-radius: 20px;
  font-size: 25px;
}

.link:hover {
  background-color: rgb(66, 66, 231);
}
.code {
  color: #000c0a;
  padding: 15px 20px;
  background-color: rgb(10, 150, 33);
  margin: 20px;
  text-decoration: none;
  border-radius: 40px;
  font-size: 25px;
}

.code:hover{
background-color: rgb(83, 202, 102);
}

footer {
  background-image: linear-gradient(rgb(242, 0, 255), rgb(92, 199, 241));
  height: 50px;
  padding: 5px;
}

.foot {
  color: #02241d;
  padding: 5px 10px;
  background-color: rgb(129, 197, 243);
  margin: 5px;
  text-decoration: none;
  border-radius: 20px;
  font-size: 15px;
}

.foot:hover {
  background-color: rgb(192, 221, 240);
  font-size: 18px;
}



.responsive{
  display:flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: space-around;
}

@media screen and (max-width: 825px) {
  .App-header {
    font-size: 25px;
    -webkit-text-stroke: 1px rgb(255, 255, 255);
  }
  nav {
    padding: 10px 0 5px 0;
  }
  .link {
    font-size: 15px;
    padding: 1px;
    margin: 1%;
  }
  .code{
    font-size:15px;
    padding:2px;
    margin:10px;
    
  }
  .foot {
    font-size: 10px;
  }
  .foot:hover {
    font-size: 12px;
  }
}

@media screen and (max-width: 400px) {

    .game{
        margin:5%;
        font-size:10px;
        padding:1px;
    }
    .code{
    font-size:10px;
    padding:1px;
  }

}


@media screen and (max-width: 265px) {
   .foot {
    font-size: 15px;
    padding:0%;
  }

}